const { Meta } = Card
function Userprofile (props) {
  // 得到token
  const token = localStorage.getItem('token')
  let username, province, id, phoneNumber
  if (token) {
    const parsedToken = JSON.parse(token)
    if (parsedToken.data) {
      username = parsedToken.data.username // 用户名
      province = parsedToken.data.province// 地区
      phoneNumber = parsedToken.data.phoneNumber// 电话号码
      // eslint-disable-next-line no-unused-vars
      id = parsedToken.data.id // id
    } else {
      console.log('parsedToken.data is undefined')
    }
  } else {
    console.log('token is undefined')
  }
  // 提交成功
  const onFinish = (values) => {
    async function getUser () {
      try {
        const response1 = await axios.get(`http://47.237.100.43:8081/user/${username}`, { headers })
        console.log(response1)
        message.success('获取用户信息成功')
      } catch (error) {
        message.error('获取用户信息失败')
        console.log(error)
      }
    }
    getUser()
    const newvalues = values
    newvalues.username = username
    newvalues.id = id
    newvalues.score = parseInt(values.score, 10)
    newvalues.phoneNumber = values.phoneNumber
    console.log('Success:', newvalues)
    const updataUser = async () => {
      try {
        const response = await axios.put('http://47.237.100.43:8081/user', newvalues, {
          headers
        })
        console.log(response)
        localStorage.setItem('token', JSON.stringify(response.data))// 更新token
        message.success('更新成功')
        window.location.reload()
      } catch (error) {
        message.error('提交失败')
        console.log(error)
      }
    }
    updataUser()
  }
  // 提交失败
  const onFinishFailed = (errorInfo) => {
    message.error('提交失败')
    console.log('Failed:', errorInfo)
  }
  // 获取分数
  let score
  if (token) {
    const parsedToken = JSON.parse(token)
    if (parsedToken.data) {
      score = parsedToken.data.score
    } else {
      console.log('parsedToken.data is undefined')
    }
  } else {
    console.log('token is undefined')
  }
  return (
    <div style={{ backgroundColor: '#f7faf7' }}>
      <Businesspage name="个人主页"></Businesspage>
      <div className="Userprofile-description">
        <Card style={{ width: 300 }}>
          <Meta
            avatar={
              <Avatar src="https://xsgames.co/randomusers/avatar.php?g=pixel" />
            }
            title={username}
            description={
              (
                <div>
                  <div>地区：{province}</div>
                  <div>电话号码：{phoneNumber}</div>
                  <div>高考成绩：{score}</div>
                </div>
              )
          }
          />
        </Card>
        <div style={{ marginTop: '0vh' }}></div>
      </div>
      <div className="Userprofile-complete">
        <div className="Userprofile-complete-nav">
          <span
            style={{
              marginRight: '5vw',
              fontWeight: '400',
              fontSize: '1.5em'
            }}
          >
            补全高中信息
          </span>
          <span style={{ fontSize: '0.9em', color: '#0066cc' }}>
            参加特殊类型招生报名的考生，可以不用补充以下信息
          </span>
        </div>
        <Divider />
        <div className="Userprofile-complete-alert">
          <Alert
            banner
            message={
              <Marquee pauseOnHover gradient={false}>
                1、考生生源所在省市和高中所学科类需要如实填写，补全信息后不允许更改；2、以下信息不影响强基计划和高校专项报名。
              </Marquee>
            }
          />
        </div>
        {score === null// 判断是否填写过
          ? (
          <div className="Userprofile-complete-form">
            <Form
              name="basic"
              labelCol={{
                span: 8
              }}
              wrapperCol={{
                span: 16
              }}
              style={{
                maxWidth: 600
              }}
              initialValues={{
                remember: true
              }}
              onFinish={onFinish}
              onFinishFailed={onFinishFailed}
              autoComplete="off"
            >
              <Form.Item
                name="province"
                label="所在省市"
                rules={[
                  {
                    required: true
                  }
                ]}
                style={{ width: '30vw' }}
              >
                <Select
                  placeholder="请选择考生生源所在省市!"
                  allowClear
                  options={options}
                ></Select>
              </Form.Item>
              <Form.Item
                label="所学科类"
                name="subject"
                rules={[
                  {
                    required: true,
                    message: '请输入所学科类!'
                  }
                ]}
                style={{ width: '30vw' }}
              >
                <Select
                  placeholder="请选择所学科类!"
                  allowClear
                  options={[
                    { value: 1, label: '理科' },
                    { value: 0, label: '文科' }
                  ]}
                ></Select>
              </Form.Item>
              <Form.Item
                label="预估成绩"
                name="score"
                rules={[
                  {
                    required: true,
                    message: '请输入高考成绩(或预估成绩)!'
                  },
                  {
                    validator: (_, value) => {
                      const numberValue = parseInt(value, 10) // 将字符串转换为数字
                      if (numberValue >= 0 && numberValue <= 750) {
                        return Promise.resolve()
                      }
                      return Promise.reject(new Error('分数必须在0到750之间'))
                    }
                  },
                  {
                    pattern: /^\d+$/,
                    message: '只能输入数字'
                  }
                ]}
                style={{ width: '30vw' }}
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="电话号码"
                name="phoneNumber"
                rules={[
                  {
                    required: true,
                    message: '请输入电话号码!'
                  },
                  {
                    pattern: /^\d+$/,
                    message: '只能输入数字'
                  }
                ]}
                style={{ width: '30vw' }}
              >
                <Input />
              </Form.Item>
              <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                <Button
                  type="primary"
                  htmlType="submit"
                  style={{
                    backgroundColor: 'rgb(255, 156, 14)',
                    width: '20vw',
                    height: '6vh'
                  }}
                >
                  提交
                </Button>
              </Form.Item>
            </Form>
          </div>
            )
          : (
            <Result
            status="success"
            title="已经填写个人信息!"
            subTitle="Order number: 2017182818828182881 Cloud server configuration takes 1-5 minutes, please wait."
            // extra={[
            //   <Button type="primary" key="console">
            //     Go Console
            //   </Button>,
            //   <Button key="buy">Buy Again</Button>
            // ]}
          />
            )}
      </div>

      <Footcommon></Footcommon>
    </div>
  )
}
